.abt-left:hover, .abt-right:hover {
  background-color: rgba(90,90,90,0.5);
  opacity: 1;
}

.abt-text {
  @include centrify;
  width: 400px;
  height: 400px;
  margin: 0px 0px ;
  padding: 20px 20px;
  flex-direction: row;
  justify-content: flex-start;
  overflow: scroll;
  background-color: rgba(50,50,50,0.2);
  z-index: 0;

  h1 {
    padding: 20px;
  }

  p {
    text-align: justify;
  }
}

.abt-photo {
  @include centrify;
  width: 400px;
  height: 400px;
  margin: 50px 0px;
  background-color: red;
  justify-content: center;
}
