#contact {
  padding: 50px 0 10px 0;
  background-color: white;
}

.contact-title {
  text-align: center;
  font-size: 2em;
  font-family: monospace;

}

.contact-link-cont {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 50px;
}

.contact-links {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
  padding: 40px;
  border-radius: 50%;
  border: 5px solid black;
  background-color: white;
}
