.banner-div {
  -webkit-display: flex;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  position: sticky;
  background-color: black;
  color: white;
}

.banner-home {
  font-size: 1.5em;
  margin: 20px;
  margin-left: 10%;
  padding: 5px;
  cursor: pointer;
  user-select: none;
}

.banner-links {
  padding: 30px;
}

.banner-link-items {
  padding-left: 20px;
  padding-right: 20px;
  margin: 20px;
  font-size: 1.3em;
  cursor: pointer;
  user-select: none;

  a {
    color: inherit;
  }

  a:active {
    color: red;
  }
}
