

@mixin columnFlex {
  display: flex;
  flex-direction: column;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

#welcome {
  z-index: -1;
  @include columnFlex;
  align-items: center;
  background-color: black;
  background-size: cover;
  margin: 0;
  padding: 100px 50px;
  color: #FAFAFA;
  font-family: sans-serif;
  width: 100%;
  text-shadow: -1px 0 rgba(0,0,0,0.5), 0 1px rgba(0,0,0,0.5), 1px 0 rgba(0,0,0,0.5), 0 -1px rgba(0,0,0,0.5);

  white-space: nowrap;

  .wc-head {
    text-align: center;
    animation: fadeIn 2s;

    img {
      border-radius: 50%;
      height: 240px;
      border: 2px solid white;
    }
  }


  .wc-text {

    font-family: 'Allerta Stencil', sans-serif;
    animation: fadeIn 2s;
    user-select: none;

    h5 {
      margin: 20px;
      text-align: center;
      font-size: 2em;
      color: rgb(255, 255, 255);
      font-weight: bold;
      border-bottom: 2px solid white;
      line-height: 80px;
      letter-spacing: 0.1em;
    }

    p {
      text-align: center;
      font-weight: lighter;
    }
  }
}

#welcome::after {

  content: "";
  position: absolute;
  display: block;

  height: 10px;
  bottom: 95px; /* -height */
  left:0;
  right:0;

  /* TODO Add browser prefixes */
  background:
    linear-gradient(
      45deg, transparent 33.333%,
      black 33.333%, black 66.667%,
      transparent 66.667%
    ),linear-gradient(
      -45deg, transparent 33.333%,
      black 33.333%, black 66.667%,
      transparent 66.667%
    );

    background-size: 8px 20px; /* toothSize doubleHeight */
    background-position: 0 -10px; /* horizontalOffset -height */

}
